<script>
import "@ui5/webcomponents/dist/Input.js"; 
import "@ui5/webcomponents/dist/List"; 
import "@ui5/webcomponents/dist/StandardListItem";
import "@ui5/webcomponents/dist/CustomListItem";
import "@ui5/webcomponents/dist/GroupHeaderListItem";
import axios from 'axios'

// let finderfiles = ['test','test']

// Input.addEventListener("change", function(e){
//   input_text = e.target.value;
// });
export default {
  
  name: 'boxfinder',
  props: {
    msg: String
  },
  data(){
    return{
      files:[],
      componentKey: 0,
    }
  },
  methods:{
    fetchfinder(){
      const list = document.getElementById("fetch-list");
      let input_text = '';
      while (list.firstChild) {
        list.removeChild(list.firstChild);
      }
      // this.files = ["/Users/ding/Box/資料庫/000_Project work/トラスコ中山/権限/work/システムテスト_権限テスト.xlsx","/Users/ding/Box/資料庫/000_Project work/トラスコ中山/権限/work/[システムテスト①]監査帳票テストユーザー整理.XLSX"]
      // Vue.$set(this.files)
      input_text = document.getElementById("searchInput").value
      axios.get('http://localhost:8080/search/'+ input_text)
      .then(respnose => {
        this.files = [];
        this.files.push(...respnose.data)
        this.$forceUpdate();
        this.componentKey += 1;  
        // respnose.data.forEach(res_file=>{
          
        // })
        // this.files = respnose.data;
        // Vue.set(this.files,0,respnose.data[0])
        // foreach respnose
        // const elements = this.files.forEach(title => {
        //   const el = document.createElement("ui5-li",`v-on:click="openfolder()"`);
        //   el.textContent = title;
        //   list.appendChild(el);
        // });
    })
    return this.files
    },
    openfolder(filename){
      axios.post('http://localhost:8080/open/',filename)
      .then(respnose => {
        // this.files = respnose.data;
      })
    }
  }
}
</script>

